:root{
    --card-bg-color: #fb61ba;
}

.card-container{
    background-color: var(--pink-gradient-start);
    background-image: var(--pink-gradient);
    display: grid;
    width: 20.25rem;;
    height: 18rem;
    grid-template-columns: 4fr 0.5fr;
    /* margin-left: auto; */
    border-radius: 1rem;
}

.img-container{
    min-height: 100%;
    min-width: 100%;
    overflow: hidden;
    border-radius: 1rem;
    background-color: var(--blue-gradient-start);
    background-image: var(--blue-gradient);
}

.card-image{
    height: 100%;
    width: 100%;
}

.card-body{
    width: 100%;
    overflow: hidden;
}

.card-text{
    /*padding: 0px 0px 0px 10px;*/
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
    /* height: 50px; */
 
}

.card-text a{
  color: white;
  text-decoration: none;
}

.card-text h5{
    width: 16rem;
    /* transform: rotate(180deg); */
}

.title {
    font-family: Georgia, 'Times New Roman', Times, serif;
    padding: 0px;
    margin: 0px;
  }

.subtitle {
    font-family: Georgia, 'Times New Roman', Times, serif;
    padding: 0px;
    margin: 0px;
}

header {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
  }

.card-deck {
    /* background-color: Gray; */
    max-width: calc(100vw - 12rem);
    min-height: 10em;
    max-height: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-flow: row;
    grid-auto-rows: minmax(1em, auto);
    grid-gap: 1em;
    gap: 1em;
    margin-left: 13rem;
    place-items: center;
  }

#Log {
    display: flex;
    flex-direction: column;
  }


.sidebar {
    display: block;
    flex-direction: column;

    position: fixed;
    left: 0;
    top: 25%;

    margin: 0;
    height: -webkit-max-content;
    height: max-content;
    width: 12rem;
    justify-content: center;

    background-color: var(--pink-gradient-start);
    background-image: var(--pink-gradient);
    border-top-right-radius: 2rem;
    border-bottom-right-radius: 2rem;

    box-shadow: 0rem 0rem 0.5rem 0.1rem #88888822;
}

.sidebar-item-container{
    display: flex;
    flex-direction: column;

    height: 100%;
    background-color: var(--sidebar-inactive);
    border-top-right-radius: 2rem;
    border-bottom-right-radius: 2rem;
}
 
.sidebar-item {
    display: flex;
    flex-direction: column;
    padding: 0.5rem 0;
    margin: 0;
    text-align: left;
    color: inherit;
    text-decoration: inherit;
    outline: none;
    height: -webkit-min-content;
    height: min-content;

    box-shadow: 0rem 1rem 1rem -0.6rem var(--shadow-color);
}

.sidebar-item.sidebar-active{
    height: 100%;
    width: 100%;
    background-color: var(--sidebar-active);

    border-top-right-radius: 1rem;
    border-bottom-right-radius: 1rem;
}

.sidebar-listheader{
    margin: 0;
    width: 100%;
    text-align: inherit;
    padding: 0.5rem 0;
    padding-left: 1rem;
}

.sidebar-active .sidebar-listheader{
    width: 12rem;
    display: block;
    color: white;
    background-color: var(--blue-gradient-start);
    background-image: var(--blue-gradient);

    border-top-right-radius: 2rem;
    border-bottom-right-radius: 2rem;
}

.list-items-container{
    margin-top: 0.5rem;
    margin-left: 1rem;
    display: none;
    overflow: hidden;
}

.sidebar-active .list-items-container{
    height: 100%;

    display: flex;
    flex-direction: column;
}

.list-item{
    margin: 0.25rem;
}

.social-media-container{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  margin: 0 auto;
  place-items: center;
}

.social-media-link{
  color: white;
  font-size: 2rem;
}

.sign-in-message {
    font-family: Georgia, 'Times New Roman', Times, serif;
    text-align: center;
    font-size: 2rem;
}

.metamask-img {
    width: 50%;
    height: auto;
    grid-column: 1 / 2;
    grid-row: 1 / 2;
    justify-self: end;
    margin-left: 12rem;
    margin-right: 0px;
    padding-right: 0px;
} 

.img-div {
    justify-items: end;
    justify-self: end;
    padding-right: 0px;
    margin-right: 0px;
}

.why-metamask {
    font-family: Georgia, 'Times New Roman', Times, serif;
    text-align: center; 
    grid-column: 1 / 2;
    grid-row: 2 / 3;
    margin-left: 8rem;
}

#walletButton {
    grid-column: 2 / 3;
    justify-self: start;
    align-self: center;
    background-color: var(--blue-gradient-start);
    background-image: var(--blue-gradient);
    color: white;
    width: 50%;
    height: 50%;
    font-family: Georgia, 'Times New Roman', Times, serif;
    font-size: 2rem;
    border-radius: 1rem;
    border: 2px solid rgb(0, 162, 255);
    transition-duration: 0.3s;
    cursor: pointer;
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}

#walletButton:hover {
    background-color: rgb(98, 205, 255);
    background-image: none;

}

.wallet-page {
    width: 100%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 3fr 1fr;
}

#main {
    display: flex;
    flex-direction: column;
}


.wallet-options {

    width: 15%;
}

.wallet-dropdown {
    font-family: Georgia, 'Times New Roman', Times, serif;
    width: 15%;
}

.different-wallet {
    font-family: Georgia, 'Times New Roman', Times, serif;
    display: flex;
    flex-direction: column;
    width: 100%;
    justify-items: center;
    align-items: center;
}


*,
*:before,
*:after {
  box-sizing: border-box;
}

h2 {
    font-size:  3rem;
}

.AssetContainer {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-bottom: 8rem;
}

.AssetContent {
    width: 80%;
    height: 100%;
    background-color: white;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 2rem 4rem 2rem 22rem 2rem 4rem 2rem;
    grid-column-gap: 2rem;
    -webkit-column-gap: 2rem;
            column-gap: 2rem;
    margin: auto;
}

.AssetImage {
    grid-column: 1 / 2;
    grid-row: 1 / 5;
    justify-self: end;    
    width: auto;
    height: 30rem;
    border: 2px solid rgb(223, 223, 223);
    border-radius: 1rem;
}

.AssetButtonContainer {
    justify-self: start;
}

.button {
    background-image: var(--pink-gradient);
    background-color: var(--pink-gradient-start);
    width: 4rem;
    height: auto;
    cursor: pointer;
    transition-duration: 0.2s;

}

.button:hover {
    background-image: var(--pink-gradient-start);
    background-color: lightpink;
}

.priceField {
    grid-column: 1 / 3;
    grid-row: 5 / 7;
    justify-self: center;
}

.renderToggles {
    grid-column: 1 / 3;
    align-self: center;
    grid-row: 7 / 8; 
    justify-self: center;
    display: flex;
    flex-direction: row;
}

.tokenName {
    grid-column: 2 / 3;
    grid-row: 2 / 3;
    justify-self: start;
}

.tokenCollection {
    grid-column: 2 / 3;
    grid-row: 1 / 2;
    justify-self: start;
}

.tokenOwner {
    grid-column: 2 / 3;
    grid-row: 3 / 4;
    justify-self: start;
}

